  // ######### 1111111
  contractMethodCall = (contract, methodName, param = []) => {

    if (!(contract instanceof Object) || methodName.constructor != String) {
      console.log('input param type error!!!')
      console.log(`contract:${typeof (contract)}`);
      console.log(`methodName:${typeof (methodName)}`);
      return 'error';
    }

    if (param.constructor != Array)
      param = [param];

    return new Promise(resolve => {
      contract.methods[methodName](...param).call().then(async result => { resolve(result) });
    })
  }
  // ######### 222222222
  toStringDecimals = (numStr, decimals) => {
    numStr = numStr.toLocaleString().replace(/,/g, '');
    decimals = decimals.toString();

    var str = (10 ** decimals).toLocaleString().replace(/,/g, '').slice(1);

    var res = (numStr.length > decimals ?
      numStr.slice(0, numStr.length - decimals) + '.' + numStr.slice(numStr.length - decimals) :
      '0.' + str.slice(0, str.length - numStr.length) + numStr).replace(/(0+)$/g, "");

    return res.slice(-1) == '.' ? res + '00' : res;
  }

